/** Media Queries **/
@font-face {
  font-family: "led";
  src: url("../fonts/TheLedDisplaySt.ttf");
}
@font-face {
  font-family: "impact";
  src: url("../fonts/321impact.ttf");
}
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  background-color: #000000;
}

p {
  color: #FFFFFF;
  font-size: 2rem;
}

.container {
  width: 75%;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "impact";
  margin: 2.5rem 0;
  font-weight: 300;
  color: #FFFFFF;
  margin: 0;
  padding: 0;
}

h1 {
  font-size: 3.8rem;
}
@media (min-width: 950px) {
  h1 {
    font-size: 8rem;
  }
}

h2 {
  font-size: 3.2rem;
}
@media (min-width: 950px) {
  h2 {
    font-size: 6rem;
  }
}

h3 {
  font-size: 2.6rem;
}
@media (min-width: 950px) {
  h3 {
    font-size: 4.5rem;
  }
}

p {
  font-size: 1.6rem;
}
@media (min-width: 950px) {
  p {
    font-size: 2rem;
  }
}

.no-filter {
  filter: none !important;
}

.header {
  width: 100%;
  background-size: cover;
  background-repeat: no-repeat;
  background-image: url("../img/header.jpg");
  background-position: center center;
  position: relative;
  height: 50vh;
}
.header.inicio {
  background-position: 65% 100%;
  background-image: url("../img/mainheader.jpg");
  height: 50vh;
}
@media (min-width: 950px) {
  .header.inicio {
    height: 100vh;
    background-position: bottom right;
  }
}
.header.inicio .menu-bar {
  background-color: rgba(0, 0, 0, 0.6);
}
@media (min-width: 950px) {
  .header.inicio .menu-bar {
    background-color: transparent;
  }
}
.header .menu-bar {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 1rem;
  position: absolute;
  background-color: rgba(0, 0, 0, 0.6);
  font-family: "impact";
}
@media (min-width: 950px) {
  .header .menu-bar {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    padding-top: 1rem;
    background-color: rgba(0, 0, 0, 0.6);
    font-family: "impact";
    visibility: visible;
    opacity: 1;
    height: auto;
  }
}
.header .menu-bar a {
  color: #FFFFFF;
  font-size: 3.4rem;
  text-transform: uppercase;
  text-shadow: 3px 3px 5px black;
  text-align: center;
  width: 100%;
  filter: blur(1.5px);
}
.header .menu-bar a:hover {
  filter: none;
  background-color: #000000;
}
@media (min-width: 950px) {
  .header .menu-bar a:hover {
    background-color: transparent;
  }
}
@media (min-width: 950px) {
  .header .menu-bar a {
    font-size: 4.2rem;
    filter: blur(3px);
    width: auto;
  }
}
@media (min-width: 1024px) {
  .header .menu-bar a {
    font-size: 5rem;
  }
}
@media (min-width: 1400px) {
  .header .menu-bar a {
    font-size: 5.5rem;
  }
}

.mobile-menu {
  padding-top: 2rem;
  background-color: rgba(0, 0, 0, 0.6);
}
@media (min-width: 950px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 3.5rem;
  margin: auto;
  cursor: pointer;
}

.mostrar {
  display: none !important;
}
@media (min-width: 950px) {
  .mostrar {
    display: flex !important;
  }
}

.index-title {
  display: flex;
  flex-direction: column;
  height: 40vh;
  justify-content: flex-end;
}
@media (min-width: 950px) {
  .index-title {
    height: 95vh;
  }
}
.index-title h1 {
  font-family: "led";
  color: #ee00f9;
  font-size: 5rem;
  line-height: 1.2;
  padding-left: 1rem;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: pruebas 2s linear infinite;
}
@media (min-width: 350px) {
  .index-title h1 {
    font-size: 7rem;
  }
}
@media (min-width: 480px) {
  .index-title h1 {
    font-size: 9rem;
    padding-left: 2rem;
  }
}
@media (min-width: 950px) {
  .index-title h1 {
    text-shadow: -4px -4px 2px #010951;
    padding-left: 4rem;
    font-size: 15rem;
  }
}
@media (min-width: 1024px) {
  .index-title h1 {
    text-shadow: -4px -4px 3px #010951;
  }
}
@media (min-width: 1400px) {
  .index-title h1 {
    text-shadow: none;
    font-size: 17rem;
  }
}

@keyframes pruebas {
  0% {
    background-image: linear-gradient(to bottom, white 0%, #ee00f9 10%);
  }
  10% {
    background-image: linear-gradient(to bottom, white 15%, #ee00f9 25%);
  }
  20% {
    background-image: linear-gradient(to top, white 20%, #ee00f9 30%);
  }
  30% {
    background-image: linear-gradient(to top, white 25%, #ee00f9 35%);
  }
  40% {
    background-image: linear-gradient(to bottom, #ee00f9 20%, white 40%, #ee00f9 50%);
  }
  50% {
    background-image: linear-gradient(to bottom, #ee00f9 25%, white 55%, #ee00f9 65%);
  }
  60% {
    background-image: linear-gradient(to top, white 20%, #ee00f9 30%);
  }
  70% {
    background-image: linear-gradient(to top, white 30%, #ee00f9 40%);
  }
  80% {
    background-image: linear-gradient(to bottom, white 10%, #ee00f9 20%);
  }
  90% {
    background-image: linear-gradient(to bottom, #ee00f9 10%, white 55%, #ee00f9 65%);
  }
  100% {
    background-image: linear-gradient(to top, white 20%, #ee00f9 30%);
  }
}
.footer {
  padding: 5rem 0;
  background-color: #010951;
}
.footer p {
  padding: 0;
  margin: 0;
  text-align: center;
}